<template>
  <a v-bind:aria-label="event.title + ' starting at: ' + eventStartTime.format('MMMM D h:mm a z')" class="event" v-bind:href="'https://www.twitch.tv/events/' + event.id">
    <img v-bind:src="event.imageURL.replace('{width}', '640').replace('{height}', '360')"/>
    <div class="date" v-if="!isMultiDay" :title="eventStartTime.format('dddd, MMMM Do YYYY, h:mm a')">
      <time :datetime="eventStartTime.format()">
        <div class="day">{{eventStartTime.format('D')}}</div>
        <div class="weekday-year">
          <span class="weekday">{{eventStartTime.format('dddd')}}</span>
          <span>{{eventStartTime.format('MMMM YYYY')}}</span>
        </div>
      </time>
    </div>

    <div class="date" v-if="isMultiDay" :title="eventStartTime.format('dddd, MMMM Do YYYY, h:mm a')">
      <time :datetime="eventStartTime.format()">
        <div class="date">
          <div class="day">{{eventStartTime.format('D')}}</div>
          <div class="weekday-year">
            <span class="weekday">{{eventStartTime.format('ddd')}}</span>
            <span>{{eventStartTime.format('MMM YY')}}</span>
          </div>
        </div>
      </time>
      -
      <div class="date" :title="eventEndTime.format('dddd, MMMM Do YYYY, h:mm a')">
        <time :datetime="eventEndTime.format()">
          <div class="date">
            <div class="day">{{eventEndTime.format('D')}}</div>
            <div class="weekday-year">
              <span class="weekday">{{eventEndTime.format('ddd')}}</span>
              <span>{{eventEndTime.format('MMM YY')}}</span>
            </div>
          </div>
        </time>
      </div>
    </div>
    <div class="pink-circle title">
      <span>{{event.title}}</span>
    </div>
    <div class="pink-circle time" v-bind:class="{ live: isLive }">
        <span class="schdeuled" v-if="isLive && isMultiDay">Now - {{eventEndTime.format('ddd h:mma z')}}</span>
        <span class="schdeuled" v-if="isLive && !isMultiDay">Now - {{eventEndTime.format('h:mma z')}}</span>
        <span class="schdeuled" v-if="!isLive && isMultiDay">{{eventStartTime.format('ddd h:mma')}} - {{eventEndTime.format('ddd h:mma z')}}</span>
        <span class="schdeuled" v-if="!isLive && !isMultiDay">{{eventStartTime.format('h:mma')}} - {{eventEndTime.format('h:mma z')}}</span>
    </div>
  </a>
</template>

<script>
import StreamStatus from '../global/StreamStatus'
import MomentTZ from 'moment-timezone'
export default {
  name: 'event',
  props: ['event'],
  components: {StreamStatus},
  computed: {
    eventStartTime: function () {
      return MomentTZ.tz(this.event.startAt, MomentTZ.tz.guess())
    },
    eventEndTime: function () {
      return MomentTZ.tz(this.event.endAt, MomentTZ.tz.guess())
    },
    isLive: function () {
      var now = MomentTZ.tz()
      return now.isAfter(this.eventStartTime) && now.isBefore(this.eventEndTime)
    },
    isMultiDay: function () {
      return this.eventStartTime.dayOfYear() !== this.eventEndTime.dayOfYear()
    },
    isMultiMonth: function () {
      return this.isMultiDay && (this.eventStartTime.month() !== this.eventEndTime.month())
    }
  }
}
</script>

<style scoped lang="scss">
@import '../../variables.scss';
.event{
  display: inline-block;
  margin: $standard-spacing;
  color: $font-white;
  width: 280px;
  vertical-align: top;
  img {
    width: 100%;
    transition: $standard-transition;
  }
  span {
    display: block;
  }
  .day {
    font-size: 2.8em;
    margin-top: $standard-spacing/4;
    color: $font-white;
  }
  .weekday{
    text-align: left;
    color: $light-purple;
    text-shadow: 0 0 35px #000;
    transition: $standard-transition;
    font-size: 1em;
    span{
      display: inline-block;
    }
  }
  .multi-month {
    display: flex;
    justify-content: space-between;
    span {
      display: inline-block;
    }
  }
  .date {
    div {
      display: inline-block;
    }
  }
  .pink-circle {
    display: block;
    position: relative;
    border-radius: 50px;
    padding: 5px;
    color: $font-white;
    border: 1px solid $light-purple;
    margin: $standard-spacing $standard-spacing-half;
    min-height: $standard-spacing3;
    transition: $standard-transition;
    &.title{
      display: flex;
      justify-content: center;
      flex-direction: column;
      transition: $standard-transition;
      span{
        display: inline-block;
      }
    }
    &.time {
      min-height: $standard-spacing2;
      white-space: nowrap;
      border-color: $font-gray;
      background-color: $font-gray-light;
      font-size: 16px;
      color: $dark-purple;
      span {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
      &.live{
        background-color: $light-purple;
        border-color: $light-purple;
        color: $font-white;
      }
    }
  }
  &:hover, &:focus {
    img {
      transform: scale(1.1);
    }
    .date{
      .weekday{
        color: $font-white;
      }
    }
    .title{
      background-color: $light-purple;
    }
  }
}
</style>



