import Vue from 'vue'
import MomentTZ from 'moment-timezone'
var twitchHeader = {headers: {'Client-ID': 'oj79pe90wcf8nan97jzuy6dm2h7fh0', 'Accept': 'application/vnd.twitchtv.v5+json'}}

/*eslint-disable */
export default {
  isStreamLive (channelId) {
    var url = 'https://api.twitch.tv/kraken/streams/' + channelId
    var p1 = new Promise(function (resolve, reject) {
      Vue.http.get(url, twitchHeader).then(response => {
        resolve(response)
      }, response => {
        // error goes here
        reject(response)
      })
    })
    return p1
  },
  getChannelInfo (channelId) {
    var url = 'https://api.twitch.tv/kraken/channels/' + channelId
    var p1 = new Promise(function (resolve, reject) {
      Vue.http.get(url, twitchHeader).then(response => {
        resolve(response)
      }, response => {
        // error goes here
        reject(response)
      })
    })
    return p1
  },
  getEvents () {
    var header = {headers: {
      'Authorization': 'OAuth hxtujcaiym0vjl9f1hntt5rg5lnp67',
      'Client-Id': 'oj79pe90wcf8nan97jzuy6dm2h7fh0',
      'Content-Type': 'application/json'
    }}
    var BodyText = [{
      "operationName": "EventsPage_EventScheduleQuery",
      "variables": {
        "channelLogin": "lowco",
        "limit": 20,
        "before": null,
        "after": MomentTZ.utc().format(),
        "sortOrder": "DESC"
      },
      "extensions": {
        "persistedQuery": {
          "version": 1,
          "sha256Hash": "b7a58e1d25ac12eda1d37b0b1545a23a404e4520803ee208b8b6c15486619fe5"
        }
      }
    }]
    var url = 'https://gql.twitch.tv/gql'
    var p1 = new Promise(function (resolve, reject) {
      Vue.http.post(url, BodyText, header).then(response => {
        resolve(response)
      }, response => {
          // error goes here
          reject(response)
      })
    })
    return p1
  }
}



